* {
   font-family: Helvetica, Arial, sans-serif;
}

body {
   padding: 60px;
}

.card {
   align-content: center;
   border: 1px solid grey;
   border-radius: 6px;
   margin-bottom: 10px;
   margin-right: 10px;
   overflow: hidden;
}

.card-grid {
   display: flex;
   flex-flow: row wrap;
}

.card img {
   max-height: 170px;
}
